import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, FatorRiscoAmbienteTrabalho } from '../../../../models/dtos/mfp/fatorRiscoAmbienteTrabalho';

interface GrupoEventoEditViewProps extends EditViewProps<FatorRiscoAmbienteTrabalho> {}

const width: number = 120;

const GrupoEventoEditView: FC<GrupoEventoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<FatorRiscoAmbienteTrabalho>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Manutenção de Fator de Risco do Ambiente de Trabalho">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline
          formProps={{ ...formProps, validation: { required: true } }}
          id="codigo"
          isPrependLabel={true}
          label="Código"
          labelWidth={width}
          placeholder="Informe o Código"
          inputMaskedProps={{ mask: '99.99.9999' }}
          type={InputType.MASK}
        />
        <InputGroupInline
          formProps={{ ...formProps, validation: { required: true, maxLength: 1000 } }}
          id="descricao"
          isPrependLabel={true}
          label="Descrição"
          labelWidth={width}
          placeholder="Informe a Descrição."
          type={InputType.TEXT_AREA}
        />
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoEventoEditView;
